import React from 'react';
import { Button } from '../../atoms/button/Button';
import './ImageBox.css';

interface ImageBoxProps {
  
  /**
   * className 설정
   */
  className?: string[];

  /**
   * style 정의
   */
  style?: object;

  /**
   * Buttons 정리
   */
  buttons?: string[];
}

export const ImageBox = ({ style, className,
  buttons
}: ImageBoxProps) => (
  <div
    style={style}
    className={`${className.join(' ')}`}
  >
    {
      buttons.map(button => (
        <Button label={ button } />
      ))
    }
  </div>
);

/**
 * react/require-default-props
 */
ButtonGroup.defaultProps = {
  style: {},
  className: [],
  buttons:[],
};

export default ButtonGroup;
